@import url(https://rsms.me/inter/inter.css);
html,
body,
#root {
  position: relative;
  margin: 0;
  padding: 0;
  overflow: hidden;
  outline: none;
  width: 100vw;
  height: 100vh;
}

body {
  background: rgb(80, 75, 75);
  font-family: "Inter var", sans-serif;
  display: flex;
  justify-content: center;
}

.picker {
  width: 90px !important;
  height: 90px !important;
  margin-bottom: 40px;
  padding-left: 20px;
}

.items {
  display:flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  position: absolute;
  left: 0;
  top:0;
}

.fade {
  -webkit-animation: fade-in-keyframes 1s;
          animation: fade-in-keyframes 1s;
}
@-webkit-keyframes fade-in-keyframes {
  from {left: "0px"}
  to {left: "100px"}
}
@keyframes fade-in-keyframes {
  from {left: "0px"}
  to {left: "100px"}
}
